import { useOutsideClick } from '@hooks/useOutsideClick';
import { useRef, useState } from 'react';

import { menuItems } from '../HeaderContainer/config';
import {
  BurgerBar,
  BurgerContainer,
  MenuButton,
  MenuLink,
  MenuList,
} from './styled';

export const BurgerMenu = () => {
  const [isOpen, setIsOpen] = useState(false);

  const menuRef = useRef<HTMLDivElement>(null);

  const toggleMenu = () => {
    setIsOpen((prevState) => !prevState);
  };

  const handleClose = () => {
    if (isOpen) {
      setIsOpen(false);
    }
  };

  useOutsideClick({
    ref: menuRef,
    handler: handleClose,
  });

  return (
    <BurgerContainer ref={menuRef}>
      <MenuButton onClick={toggleMenu} className={isOpen ? 'open' : ''}>
        <BurgerBar className="bar1" />
        <BurgerBar className="bar2" />
        <BurgerBar className="bar3" />
      </MenuButton>
      <MenuList isOpen={isOpen}>
        {menuItems.map(({ id, path, text }) => (
          <MenuLink key={id} to={path} onClick={handleClose}>
            {text}
          </MenuLink>
        ))}
      </MenuList>
    </BurgerContainer>
  );
};
