import { Nav, NavLinks, StyledNavLink } from './styled';
import { NavbarProps } from './types';

export const Navbar = ({ menuItems }: NavbarProps) => {
  return (
    <Nav>
      <NavLinks>
        {menuItems.map(({ id, text, path }) => (
          <StyledNavLink
            key={id}
            to={path}
            className={({ isActive }) => (isActive ? 'active' : '')}
          >
            {text}
          </StyledNavLink>
        ))}
      </NavLinks>
    </Nav>
  );
};
