import { NavLink } from 'react-router-dom';
import { styled } from 'styled-components';

export const Nav = styled.nav`
  max-width: ${({ theme }) => theme.size.elementSize.m};
  width: 100%;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    display: none;
  }
`;

export const NavLinks = styled.ul`
  display: flex;
  justify-content: space-between;
`;

export const StyledNavLink = styled(NavLink)`
  position: relative;

  color: ${({ theme }) => theme.colors.primaryText};

  font-size: ${({ theme }) => theme.font.fontSizes.l};
  font-weight: ${({ theme }) => theme.font.fontWeights.bold};

  &:hover {
    color: ${({ theme }) => theme.colors.activeLink};
  }

  &.active {
    color: ${({ theme }) => theme.colors.activeLink};

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -5px;

      height: 2px;

      background-color: ${({ theme }) => theme.colors.activeLink};
    }
  }
`;
