import { stocks as mockStocks } from '@constants/mocks/stocks';
import { createSlice } from '@reduxjs/toolkit';

import { StockState } from '@/types/financial';

const initialState: StockState = {
  stocks: mockStocks,
};

const stockSlice = createSlice({
  name: 'stocks',
  initialState,
  reducers: {},
});

export default stockSlice.reducer;
