import CurrencyTrackerLogo from '@images/CurrencyTrackerLogo.svg';

import {
  FooterTextContainer,
  Text,
  TitleContainer,
  TitleHeader,
  TitleImage,
} from './styled';
import { FooterTextProps } from './types';

export const FooterText = (props: FooterTextProps) => {
  const { title, text } = props;
  return (
    <FooterTextContainer>
      <TitleContainer>
        <TitleImage src={CurrencyTrackerLogo} alt="Company Logo" />
        <TitleHeader>{title}</TitleHeader>
      </TitleContainer>
      <Text>{text}</Text>
    </FooterTextContainer>
  );
};
