import { lazy } from 'react';
import { RouteObject } from 'react-router-dom';

import { Routes } from './paths';

const Home = lazy(() =>
  import('@pages/Home').then((module) => ({ default: module.Home })),
);
const Timeline = lazy(() =>
  import('@pages/Timeline').then((module) => ({ default: module.Timeline })),
);
const BankCard = lazy(() =>
  import('@pages/BankCard').then((module) => ({ default: module.BankCard })),
);
const Contacts = lazy(() =>
  import('@pages/Contacts').then((module) => ({ default: module.Contacts })),
);
const NotFound = lazy(() =>
  import('@pages/NotFound').then((module) => ({ default: module.NotFound })),
);

export const ROUTES: RouteObject[] = [
  { path: Routes.Home, element: <Home /> },
  { path: Routes.Timeline, element: <Timeline /> },
  { path: Routes.BankCard, element: <BankCard /> },
  { path: Routes.Contacts, element: <Contacts /> },
  { path: Routes.NotFound, element: <NotFound /> },
];
