import { API_CONFIG } from '@constants/config';
import axios from 'axios';

import { FetchExchangeRate } from '@/types/financial';

export const fetchExchangeRate = async ({
  baseCurrency,
  amount,
  targetCurrency,
}: FetchExchangeRate): Promise<number> => {
  try {
    const response = await axios.get(
      `${API_CONFIG.CURRENCY_API_URL}${baseCurrency}`,
    );

    const rate = response.data.rates[targetCurrency];
    if (!rate) {
      throw new Error('We cant convert to this value');
    }

    const convertedAmount = Number((rate * amount).toFixed(2));
    return convertedAmount;
  } catch (error) {
    console.error(error);
    throw new Error(`We have no info about this exchange`);
  }
};
