import { fetchCurrenciesData } from '@api/coin/fetchCurrencies';
import { fetchExchangeRate } from '@api/currency/fetchExchange';
import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';

import { Currency, CurrencyState, FetchExchangeRate } from '@/types/financial';

export const fetchCurrenciesThunk = createAsyncThunk(
  'currencies/fetchCurrencies',
  async () => {
    return await fetchCurrenciesData();
  },
);

export const fetchExchangeRateThunk = createAsyncThunk(
  'currencies/fetchExchangeRate',
  async ({ baseCurrency, amount, targetCurrency }: FetchExchangeRate) => {
    return await fetchExchangeRate({ baseCurrency, amount, targetCurrency });
  },
);

const initialState: CurrencyState = {
  currencies: [],
  isLoading: false,
  error: null,
  lastUpdated: null,

  conversionResult: null,
  isConverting: false,
  conversionError: null,

  filterResult: [],
};

const currencySlice = createSlice({
  name: 'currencies',
  initialState,
  reducers: {
    filterCurrencies: (
      state,
      action: PayloadAction<{ searchItem: string; excludedCurrency: string }>,
    ) => {
      const { searchItem, excludedCurrency } = action.payload;
      state.filterResult = state.currencies
        .filter(
          (currency) =>
            currency.name !== excludedCurrency &&
            currency.name.toLowerCase().includes(searchItem.toLowerCase()),
        )
        .sort((a, b) => {
          const aMatch = a.name
            .toLowerCase()
            .startsWith(searchItem.toLowerCase());
          const bMatch = b.name
            .toLowerCase()
            .startsWith(searchItem.toLowerCase());

          if (aMatch && !bMatch) return -1;
          if (!aMatch && bMatch) return 1;
          return a.name.localeCompare(b.name);
        });
    },

    resetConversionResult(state) {
      state.conversionResult = null;
      state.conversionError = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchCurrenciesThunk.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(
        fetchCurrenciesThunk.fulfilled,
        (state, action: PayloadAction<Currency[]>) => {
          state.isLoading = false;
          state.currencies = action.payload;
          state.lastUpdated = new Date().toLocaleTimeString([], {
            hour: '2-digit',
            minute: '2-digit',
          });
        },
      )
      .addCase(fetchCurrenciesThunk.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.error.message || 'Failed to fetch currencies';
      })

      .addCase(fetchExchangeRateThunk.pending, (state) => {
        state.isConverting = true;
        state.conversionError = null;
      })
      .addCase(fetchExchangeRateThunk.fulfilled, (state, action) => {
        state.isConverting = false;
        state.conversionResult = action.payload;
      })
      .addCase(fetchExchangeRateThunk.rejected, (state, action) => {
        state.isConverting = false;
        state.conversionError =
          action.error.message || 'Failed to fetch exchange rate';
      });
  },
});

export const { filterCurrencies, resetConversionResult } =
  currencySlice.actions;
export default currencySlice.reducer;
