import {
  FooterLinksContainer,
  GroupTitle,
  Link,
  LinksContainer,
} from './styled';
import { FooterLinkProps } from './types';

export const FooterLinks = (props: FooterLinkProps) => {
  const { title, links } = props;
  return (
    <FooterLinksContainer>
      <GroupTitle>{title}</GroupTitle>
      <LinksContainer>
        {links.map(({ id, text }) => (
          <Link key={id}>{text}</Link>
        ))}
      </LinksContainer>
    </FooterLinksContainer>
  );
};
