import { createGlobalStyle } from 'styled-components';

export const GlobalStyles = createGlobalStyle`
    *{
        margin: 0;
        padding: 0;
        
        box-sizing: border-box;
    }

    body{
        background-color: ${({ theme }) => theme.colors.background};
        color: ${({ theme }) => theme.colors.primaryText};
    }

    a{
        text-decoration: none;
        color: inherit
    }

    button{
        background: none;
        border: none;
        padding: 0;
        cursor: pointer;
    }

    ul{
        list-style-type: none;
    }

    input{
        background: none;
        border: none;
        padding: 0;
        font-family: inherit;
        outline: none;
    }

    /* TODO [https://sharing.clickup.com/9012257271/t/h/8696d26qx/ECU49EL50E0K3RE] - add fonts */

`;
