import { NavLink } from 'react-router-dom';
import { styled } from 'styled-components';

import { MenuListProps } from './types';

export const BurgerContainer = styled.div`
  display: none;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    display: block;
  }
`;

export const MenuButton = styled.button`
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &.open {
    .bar1 {
      transform: translateY(10px) rotate(45deg);
    }
    .bar2 {
      opacity: 0;
    }
    .bar3 {
      transform: translateY(-12px) rotate(-45deg);
    }
  }
`;
export const BurgerBar = styled.div`
  width: 30px;
  height: 3px;
  border-radius: 5px;
  background: ${({ theme }) => theme.colors.primaryText};
  margin: 4px 0;
  transition: transform 0.3s;
`;

export const MenuList = styled.ul<MenuListProps>`
  width: 100%;

  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;

  display: flex;
  flex-direction: column;

  background-color: ${({ theme }) => theme.colors.burgerBackground};

  padding: ${({ theme }) => theme.spacing.l};

  visibility: ${({ isOpen }) => (isOpen ? 'visible' : 'hidden')};
  transform: ${({ isOpen }) =>
    isOpen ? 'translateY(0)' : 'translateY(-100%)'};

  transition:
    transform 0.3s ease-in-out,
    visibility 0.3s;
`;

export const MenuLink = styled(NavLink)`
  padding: ${({ theme }) => theme.spacing.l} 0;

  color: ${({ theme }) => theme.colors.primaryText};

  font-size: ${({ theme }) => theme.font.fontSizes.l};

  &.active {
    color: ${({ theme }) => theme.colors.activeLink};
  }
`;
