import { API_CONFIG } from '@constants/config';
import axios from 'axios';

import { Currency, FinancialIcon } from '@/types/financial';

export const fetchCurrenciesData = async (): Promise<Currency[]> => {
  const { COIN_API_KEY, COIN_API_URL, COIN_API_ICON_URL } = API_CONFIG;

  const response = await axios.get(`${COIN_API_URL}`, {
    headers: { 'X-CoinAPI-Key': COIN_API_KEY },
  });

  const iconsResponse = await axios.get(`${COIN_API_ICON_URL}`, {
    headers: { 'X-CoinAPI-Key': COIN_API_KEY },
  });

  const iconsMap: { [key: string]: string } = {};
  iconsResponse.data.forEach((icon: FinancialIcon) => {
    iconsMap[icon.asset_id] = icon.url;
  });

  return response.data
    .filter((currency: Currency) => currency.price_usd !== undefined)
    .slice(0, 9)
    .map((currency: Currency) => ({
      asset_id: currency.asset_id,
      name: currency.name,
      price_usd: currency.price_usd,
      icon_url: iconsMap[currency.asset_id] || null,
    })) as Currency[];
};
