import { FooterLinks } from '../FooterLinks';
import { FooterText } from '../FooterText';
import { footerArticleData, footerLinksData } from './config';
import { FooterContainer, LinksList } from './styled';

export const Footer = () => {
  return (
    <FooterContainer>
      <FooterText
        title={footerArticleData.title}
        text={footerArticleData.text}
      />
      <LinksList>
        {footerLinksData.map(({ id, title, links }) => (
          <FooterLinks key={id} title={title} links={links} />
        ))}
      </LinksList>
    </FooterContainer>
  );
};
