import { styled } from 'styled-components';

export const FooterTextContainer = styled.article`
  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  display: flex;
  flex-direction: column;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    max-width: ${({ theme }) => theme.size.elementSize.m};
  }
`;

export const TitleContainer = styled.div`
  display: flex;
`;

export const TitleImage = styled.img`
  width: ${({ theme }) => theme.size.imageSize.s};
  height: ${({ theme }) => theme.size.imageSize.s};
`;

export const TitleHeader = styled.h1`
  margin-left: ${({ theme }) => theme.spacing.m};

  font-size: ${({ theme }) => theme.font.fontSizes.l};
  font-weight: ${({ theme }) => theme.font.fontWeights.bold};

  background: ${({ theme }) => theme.colors.gradientText};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    ${({ theme }) => theme.font.fontSizes.m};
  }
`;

export const Text = styled.p`
  margin-top: ${({ theme }) => theme.spacing.l};

  ${({ theme }) => theme.font.fontSizes.m};
`;
